import React from 'react';

import Layout from '../components/Layout';

// import { Link } from 'gatsby';
import Sidebar from '../components/Sidebar';
import config from '../../config';
const IndexPage = () => (
  <Layout>
    <Sidebar />
    <div className="container-fluid p-0">
      <section
        className="resume-section p-3 p-lg-5 d-flex align-items-center"
        id="about"
      >
        <div className="w-100">
          <h1 className="mb-0">
            {config.firstName}
            <span className="text-primary">{config.lastName}</span>
          </h1>
          <div className="subheading mb-5">
            {config.address} · {config.phone} ·
            <a href={`mailto:${config.email}`}>{config.email}</a>
          </div>
          <p className="lead mb-5">
            Have 6 years of software development experience working on challenging projects from conception to deployment and maintenance with a
            Bachelors of technology in Information technology.
            Experienced Web Developer adept in all stages
            of advanced web development. Knowledgeable
            in user interface, testing, and debugging
            processes. Bringing forth expertise in design,
            installation, testing and maintenance of web
            systems. Equipped with a diverse and promising
            skill-set.
          </p>
          <div className="social-icons">
            {config.socialLinks.map(social => {
              const { icon, url } = social;
              return (
                <a key={url} href={url}>
                  <i className={`fab ${icon}`}></i>
                </a>
              );
            })}
          </div>
        </div>
      </section>

      <hr className="m-0" />

      <section
        className="resume-section p-3 p-lg-5 d-flex justify-content-center"
        id="experience"
      >
        <div className="w-100">
          <h2 className="mb-5">Experience</h2>

          <div className="resume-item d-flex flex-column flex-md-row justify-content-between mb-5">
            <div className="resume-content">
              <h3 className="mb-0">React Froentend Developer - Professional</h3>
              <div className="subheading mb-3">Associate Projects (Cognizant) Right Notes - Apple, Bangalore, India</div>
              <p>
                Taken the ownership of our successful long-term running software solutions.
                Migrated an existing website in can.js to React.
                Implemented and worked on Redux concept, various
                npm packages and on a licensed version of ag-grid.
                Worked closely with the client and have given several
                demos.
                Well accustomed to scrum methodologies, attended daily
                standup meetings and scrum related calls.
              </p>
            </div>
            <div className="resume-date text-md-right">
              <span className="text-primary">December 2019 - Present</span>
            </div>
          </div>

          <div className="resume-item d-flex flex-column flex-md-row justify-content-between mb-5">
            <div className="resume-content">
              <h3 className="mb-0">Angular Froentend Developer - Professional</h3>
              <div className="subheading mb-3">Associate Projects (Cognizant), Bangalore, India</div>
              <p>
                Worked on migrating the code from lower version to
                higher version of Angular.
                Delivered end to end features independently and
                contributed effectively as a team.
                Written well designed, efficient code along with
                automated unit tests and integration tests for UI.
                Participated in code reviews and provided
                recommendations to facilitate automated testing and
                improve performance.
              </p>
            </div>
            <div className="resume-date text-md-right">
              <span className="text-primary">July 2019 - November 2019</span>
            </div>
          </div>

          <div className="resume-item d-flex flex-column flex-md-row justify-content-between mb-5">
            <div className="resume-content">
              <h3 className="mb-0">FullStack Engineer</h3>
              <div className="subheading mb-3">Senior Analyst (Accenture), Bangalore, India</div>
              <p>
                Worked as requirement analyst, Designing the
                Requirement, Development, Unit Testing to Production
                Delivery and installation.
                Working as release coordinator for the application.
                Worked as SME during conversion of the application from
                Pega to AngularJS.
                Handled a small team and have led a team of 15 members
                for innovation contest.
                Created Splunk reports for jobs.
              </p>
            </div>
            <div className="resume-date text-md-right">
              <span className="text-primary">November 2014 - May 2019</span>
            </div>
          </div>
        </div>
      </section>

      <hr className="m-0" />

      <section
        className="resume-section p-3 p-lg-5 d-flex align-items-center"
        id="education"
      >
        <div className="w-100">
          <h2 className="mb-5">Education</h2>

          <div className="resume-item d-flex flex-column flex-md-row justify-content-between mb-5">
            <div className="resume-content">
              <h3 className="mb-0">BPUT, Odisha, India</h3>
              <div className="subheading mb-3">Bachelor Of Information Technology (BTech)</div>
              <p>Percentage: 77.5%</p>
            </div>
            <div className="resume-date text-md-right">
              <span className="text-primary">May 2010 - May 2014</span>
            </div>
          </div>
        </div>
      </section>

      <hr className="m-0" />

      <section
        className="resume-section p-3 p-lg-5 d-flex align-items-center"
        id="skills"
      >
        <div className="w-100">
          <h2 className="mb-5">Skills</h2>

          <div className="subheading mb-3">
            Programming Languages &amp; Tools
          </div>
          <ul className="list-inline dev-icons">
            <li className="list-inline-item">
              <i className="fab fa-html5"></i>
            </li>
            <li className="list-inline-item">
              <i className="fab fa-css3-alt"></i>
            </li>
            <li className="list-inline-item">
              <i className="fab fa-js-square"></i>
            </li>
            <li className="list-inline-item">
              <i className="fab fa-angular"></i>
            </li>
            <li className="list-inline-item">
              <i className="fab fa-vuejs"></i>
            </li>
            <li className="list-inline-item">
              <i className="fab fa-node-js"></i>
            </li>
            <li className="list-inline-item">
              <i className="fab fa-chrome"></i>
            </li>
            <li className="list-inline-item">
              <i className="fab fa-npm"></i>
            </li>
          </ul>

          <div className="subheading mb-3">Workflow</div>
          <ul className="fa-ul mb-0">
            <li>
              <i className="fa-li fa fa-check"></i>
              ASP.NET, .NET Core, C# , Data Structures, Algorithms,Google Chrome Extension development
            </li>
            <li>
              <i className="fa-li fa fa-check"></i>
              ASP.NET MVC, Razor View, Razor Pages
            </li>
            <li>
              <i className="fa-li fa fa-check"></i>
              ASP.NET Web API, WCF Service
            </li>
            <li>
              <i className="fa-li fa fa-check"></i>
              Test Driven Development (TDD)
            </li>
            <li>
              <i className="fa-li fa fa-check"></i>
              Unit Testing – xUnit, JustMock
            </li>
            <li>
              <i className="fa-li fa fa-check"></i>
              API and UI Automation- Specflow, Selenium
            </li>
            <li>
              <i className="fa-li fa fa-check"></i>
              Java script Unit Testing – eslint, karma, karma-coverage
            </li>
            <li>
              <i className="fa-li fa fa-check"></i>
              vuejs, angular, typescript, javascript, ECMAScript 6, HTML5, CSS
            </li>
            <li>
              <i className="fa-li fa fa-check"></i>
              DB SQL Server, NOSQL, Profiler, Store Procedure, Trigger
            </li>
            <li>
              <i className="fa-li fa fa-check"></i>
              Unit Testing – xUnit, JustMock
            </li>
            <li>
              <i className="fa-li fa fa-check"></i>
              Continuous Integration and Continuous Delivery (CI/CD)
            </li>
            <li>
              <i className="fa-li fa fa-check"></i>
              Blue Green Deployment
            </li>
            <li>
              <i className="fa-li fa fa-check"></i>
              Performance Analysis
            </li>
            <li>
              <i className="fa-li fa fa-check"></i>
              Agile Scrum Methodology, DevOps
            </li>
          </ul>
        </div>
      </section>

      <hr className="m-0" />

      <section
        className="resume-section p-3 p-lg-5 d-flex align-items-center"
        id="interests"
      >
        <div className="w-100">
          <h2 className="mb-5">Interests</h2>
          <p>
            Apart from being a web developer, I enjoy most of my time being
            outdoors. I like to travel.
          </p>
          <p className="mb-0">
            When forced indoors, I follow a number of sci-fi and fantasy genre
            movies and television shows, I am an aspiring chef.
          </p>
        </div>
      </section>

      <hr className="m-0" />
    </div>
  </Layout>
);

export default IndexPage;
